/* ---------------------------------------------------------------------------------------------------- */
a {
  cursor: pointer;
}
.disabled {
  pointer-events: auto;
}
.btn {
  min-height: 26px;
  min-width: 26px;
}
.btn.disabled {
  pointer-events: auto;
}
.btn.disabled:hover {
  z-index: auto;
}
.dropdown-menu > li > a.disabled,
.dropdown-menu > li > a.disabled:hover,
.dropdown-menu > li > a.disabled:focus {
  color: #999999;
}
.dropdown-menu > li > a.disabled:hover,
.dropdown-menu > li > a.disabled:focus {
  text-decoration: none;
  background-color: transparent;
  background-image: none;
  border-color: transparent;
  filter: progid:DXImageTransform.Microsoft.gradient(enabled=false);
  cursor: default;
}
/* Limit dropdown menus to 90% of the viewport size */
.dropdown-menu {
  height: auto;
  overflow-x: hidden;
  max-height: 90vh;
}
/* Align these buttons more nicely */
.btn.fa-minus,
.btn.fa-plus {
  padding-top: 4px;
}
/* HACK: Workaround for https://github.com/patternfly/patternfly/issues/174*/
.page-ct {
  margin-top: 20px;
}
.highlight-ct {
  background-color: #d4edfa;
}
/* Well and Blankslate */
.curtains-ct {
  top: 0px;
  height: 100%;
  width: 100%;
  position: fixed;
}
.panel .well {
  margin-bottom: 0px;
  border: none;
  border-radius: 0px;
  background-color: #FAFAFA;
}
.well.blank-slate-pf {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05) inset;
  padding-top: 40px;
}
.blank-slate-pf .spinner-lg {
  height: 58px;
  width: 58px;
}
/*
 * Control buttons such as play and stop
 * Used with .btn .btn-default
 */
.btn-control-ct,
.btn-control-ct:hover {
  background-position: center center;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  background-image: none;
  -webkit-border-radius: 2;
  -moz-border-radius: 2;
  border-radius: 2px;
  height: 28px;
  width: 28px;
  box-shadow: none;
}
.btn-control-ct {
  background-color: #eeeeee;
}
.btn-control-ct:hover {
  background-color: #e0e0e0;
}
/* On/off switch */
.btn-onoff-ct {
  margin: 1px 0px;
  text-transform: uppercase;
}
.btn-onoff-ct .btn {
  color: transparent;
  border-color: #B7B7B7;
  padding: 2px 6px 1px 6px;
  background-color: white;
  background-image: linear-gradient(to bottom, #fafafa 0px, #ededed 100%);
  -webkit-box-shadow: none;
  box-shadow: none;
  width: 37px;
}
.btn-onoff-ct .btn:first-child {
  border-right: #00435F;
}
.btn-onoff-ct .btn:last-child {
  border-left: #00435F;
  padding-left: 5px;
}
.btn-onoff-ct .btn.active {
  background-image: none;
  width: 36px;
}
.btn-onoff-ct .btn.active:first-child {
  background-color: #0086CF;
  color: white;
  border-right: 1px solid #0071b0;
}
.btn-onoff-ct .btn.active:last-child {
  color: #000;
  border-left: 1px solid #d6d6d6;
}
.btn-onoff-ct .btn.disabled {
  pointer-events: none;
  color: transparent !important;
}
.btn-onoff-ct .btn.active.disabled {
  background-color: #888 !important;
  color: white !important;
}
/* Small list inside a dialog */
/* Alert fixups */
/* HACK: word-wrap workaround for long alerts https://github.com/patternfly/patternfly/issues/491 */
.modal-content .alert {
  text-align: left;
  padding-top: 5px;
  padding-bottom: 5px;
  word-wrap: break-word;
}
.modal-content .alert .fa {
  position: absolute;
  left: 10px;
  top: 6px;
  font-size: 20px;
}
.modal-content .alert .pficon {
  top: 5px;
}
.alert.alert-danger .fa {
  color: #af151a;
}
/* Dialog patterns */
.dialog-wait-ct {
  margin-top: 3px;
}
.dialog-wait-ct .spinner {
  display: inline-block;
}
.dialog-wait-ct span {
  vertical-align: 4px;
  padding-left: 10px;
}
.dialog-list-ct {
  max-height: 230px;
  overflow-x: auto;
  border: 1px solid #CCC;
  margin-bottom: 0px;
}
/* HACK: https://github.com/patternfly/patternfly/issues/255 */
input[type=number] {
  padding: 0 0 0 5px;
}
/* Make a dialog visible */
.dialog-ct-visible {
  display: block;
}
/* Panels don't draw borders between them */
.panel > .table > tbody:first-child td {
  border-top: 1px solid #dddddd;
}
/* Table headers should not generate a double border */
.panel .table thead tr th {
  border-bottom: none;
}
.panel-heading {
  background: #F5F5F5;
  height: 44px;
}
/* Vertically center dropdown buttons in panel headers */
.panel-heading .btn {
  margin-top: -3px;
}
/*
 * Fix up table row hovering.
 *
 * When you hover over table rows it's because they're clickable.
 * Make the table row hover color match the list-group-item.
 */
.table-hover > tbody > tr > td,
.table-hover > tbody > tr > th,
.dialog-list-ct .list-group-item {
  cursor: pointer;
}
.table-hover > tbody > tr:hover > td,
.table-hover > tbody > tr:hover > th,
.dialog-list-ct .list-group-item:hover:not(.active) {
  background-color: #d4edfa;
}
/* Override patternfly to fit buttons and such */
.table > thead > tr > th,
.table > tbody > tr > td {
  padding: 8px;
}
/* Override the heavy patternfly headers */
.table > thead {
  background-image: none;
  background-color: #fff;
}
/* Make things line up */
.table tbody tr td:first-child,
.table thead tr th:first-child {
  padding-left: 15px;
}
.table tbody tr td:last-child,
.table thead tr th:last-child {
  padding-right: 15px;
}
.info-table-ct td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 0.75em;
  padding-top: 0.25em;
  vertical-align: top;
  line-height: 26px;
}
.info-table-ct td:first-child {
  text-align: right;
  color: #888888;
}
.info-table-ct td:not(:first-child) {
  color: black;
}
.info-table-ct td button {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.form-table-ct {
  width: 100%;
}
.form-table-ct td {
  padding-left: 0.75em;
  padding-top: 0.25em;
  line-height: 26px;
}
.form-table-ct td.top {
  vertical-align: top;
}
.form-table-ct td:first-child {
  text-align: right;
  white-space: nowrap;
  color: #888888;
  width: 5px;
  /* will be expanded by nowrap */
}
.form-table-ct td[colspan] {
  text-align: inherit;
}
.form-table-ct td {
  height: 26px;
}
.form-table-ct td.header {
  font-weight: bold;
  text-align: left;
  color: #4D5258;
  padding: 20px 0 10px 0;
}
.form-table-ct label input[type='radio'],
.form-table-ct label input[type='checkbox'] {
  margin-right: 4px;
}
.form-table-ct label {
  margin-bottom: 0px;
}
.form-table-ct label span {
  vertical-align: super;
}
/* Break up sidebar in columns in smaller sizes*/
@media (min-width: 992px) {
  .info-table-ct-container .info-table-ct {
    table-layout: fixed;
    width: 100%;
  }
}
